// eslint-disable-next-line import/no-unresolved
import $ from 'jquery';

import { restCall } from '../common/utilities.js';

class RecipeTitle {
	constructor() {
		this.elements = {
			container: $( '#titlediv' ),
			input: $( '#titlediv #title' ),
			status: {},
		};

		this.publishedTitle = this.elements.input.val();

		// Create DOM elements
		this.createStatus();

		// Bind changes
		this.bindChanges();

		// Unbind beforeunload.edit-post
		this.notSavedAlert();
	}

	createStatus() {
		const container = $( '<div/>', { class: 'uap-title-status' } ),
			uapContainer = $( '<div/>', { class: 'uap' } );

		// Saving
		const saving = $( '<div/>', { class: 'uo-status uo-status--saving' } ),
			savingIcon = $( '<span/>', { class: 'uo-status-icon uap-icon uap-icon--loading uap-icon--rotate' } ),
			savingText = $( '<span/>', { class: 'uo-status__text' } ).text( UncannyAutomator.i18n.title.saving );

		saving.append( savingIcon );
		saving.append( savingText );

		this.elements.status.saving = saving;

		// Saved
		const saved = $( '<div/>', { class: 'uo-status uo-status--saved' } ),
			savedIcon = $( '<span/>', { class: 'uo-status-icon uap-icon uap-icon--check' } ),
			savedText = $( '<span/>', { class: 'uo-status__text' } ).text( UncannyAutomator.i18n.title.saved );

		saved.append( savedIcon );
		saved.append( savedText );

		this.elements.status.saved = saved;

		uapContainer.append( saving );
		uapContainer.append( saved );
		container.append( uapContainer );

		// Append to the WP container
		this.elements.container.append( container );
	}

	setStatusSaving() {
		this.elements.status.saving.stop().fadeIn( 150 );
	}

	setStatusSaved() {
		this.elements.status.saving.stop().fadeOut( 150 );

		this.elements.status.saved.stop().fadeIn( 150, () => {
			setTimeout( () => {
				this.elements.status.saved.fadeOut( 150 );
			}, 3000 );
		} );
	}

	revertChanges() {
		// Revert the title
		this.elements.input.val( this.publishedTitle );

		// Stop the saving animation
		this.elements.status.saving.stop().fadeOut( 150 );
	}

	bindChanges() {
		let typingTimeout;

		// Bind
		this.elements.input.on( 'input', () => {
			// Clear the timeout if it has already been set.
			// This will prevent the previous task from executing
			clearTimeout( typingTimeout );

			// Wait 1.5 seconds
			typingTimeout = setTimeout( () => {
				// Set status saving
				this.setStatusSaving();

				// Update title
				this.updateTitle();
			}, 1500 );
		} );
	}

	getTitle() {
		return this.elements.input.val();
	}

	updateTitle() {
		restCall(
			'change_post_title',
			{
				// eslint-disable-next-line camelcase
				post_ID: UncannyAutomator.recipe.id,
				// eslint-disable-next-line camelcase
				post_title: this.getTitle(),
			},
			() => {
				this.setStatusSaved();

				// Update the published title
				this.publishedTitle = this.getTitle();
			},
			() => {
				// Output if the developer mode is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					console.error( "Couldn't save the title" );
				}

				// Revert the changes
				this.revertChanges();
			}
		);
	}

	notSavedAlert() {
		$( document ).ready( () => {
			$( window ).off( 'beforeunload.edit-post' );
		} );
	}
}

export default RecipeTitle;
