import $ from 'jquery';

import {
	getDOMElements,
	isEmpty,
	isDefined,
	restCall
} from '../common/utilities.js';

const sortable = require( 'html5sortable' );

import ItemHeaderMenu     from '../item/action-menu.js';
import ItemHeaderIcon     from '../item/integration-icon.js';
import ItemHeaderTag      from '../item/integration-name.js';
import ItemHeaderAsync    from '../item/button-schedule.js';
import ItemHeaderSentence from '../item/sentence.js';
import ItemReorder    	  from '../item/handle-reorder.js';
import Actions            from '../item/actions.js';

class MetaboxItems {
	constructor( itemType, items, isSortable = false ){
	
		// Get the parent element for the items
		let metabox = getDOMElements( 'metabox', {
			type: itemType
		});

		// Count items
		const itemCount = Object.keys(items).length;

		// If there is more than one sortable item
		if ( itemCount > 1 && isSortable ) {

			// Create an inner container
			let innerContainer = $( '<div/>', {
				'class':   'sortable'
			});

			// Sort items according to their menuOrder
			const sorted_items = Object.values(items).sort( ( a, b ) => {
				return a.menuOrder-b.menuOrder
			} );

			// Add each item to the container
			$.each( sorted_items, ( itemId, item ) => {
				if ( itemType == item.itemType ){
					// Create item
					let _item = this.createItem( item.id, isSortable );
	
					innerContainer.append( _item );
					
				}
			});

			// Make the container sortable
			this.makeSortable( innerContainer );

			// Append the container to the metabox
			metabox.append( innerContainer );
		
		} else {
			// Otherwise, simply add the items
			$.each( items, ( itemId, item ) => {
				if ( itemType == item.itemType ){
					// Create item
					let item = this.createItem( itemId );
	
					metabox.append( item );
					
				}
			});
		}
				
	}

	createItem( itemId, isSortable){
		// Get item data
		let item = UncannyAutomator.recipe.items[ itemId ];

		// Create container
		let container = $( '<div/>', {
			'class':   'item',
			'data-id': itemId
		});

		if ( item.isMissing ){
			container.addClass( 'item--missing' );
		}

		if ( item.meta.async_mode ){
			container.addClass( 'item--hasSchedule' );
		}

		// Replace tokens in the CSS
		if ( ! isEmpty( item.inlineCss ) ){
			item.inlineCss = item.inlineCss.replace( /{{item_id}}/g, itemId );

			// Add inline inlineCss
			container.prepend( item.inlineCss );
		}

		// Create header
		let header = this.createHeader(
			item.integrationCode,
			item.itemType,
			item.itemCode,
			itemId,
		);

		let notice = this.createNotice( item );

		if ( isSortable ) {
			let dragHandleButton = new ItemReorder();
			container.append( dragHandleButton );
		}

		container.append( header );
		container.append( notice );

		container.on( 'automator:item:open', (e, open) => {
			let asyncButtons = container.find('.item-async');
			$( asyncButtons[0] ).trigger( 'automator:asyncButton:maybeDisable', open );           
		})

		return container;
	}

	/**
	 * Creates item header
	 * 
	 * @since 0.5
	 *
	 * @param {string}    integrationCode - The integration code
	 * @param {string}    itemType - The item type: 'trigger', 'action' or 'closure'
	 * @param {string}    itemCode - The code of the item
	 * @param {int}       itemId - The ID of the item
	 */

	createHeader( integrationCode, itemType, itemCode, itemId ){
		let container = $( '<div/>' ),
			title     = $( '<div/>', { 'class': 'item-title uap-clear' });

		// Get item data
		let item = UncannyAutomator.recipe.items[ itemId ];

		// If the item is missing remove the integrationCode
		// We don't want to show the integration icon
		if ( item.isMissing ){
			integrationCode = null;
		}
		
		let actions = new ItemHeaderMenu( itemType, integrationCode, itemCode, itemId );
		container.append( actions );

		let icon    = new ItemHeaderIcon( integrationCode );
		container.append( icon );

		// Integration name for missing items
		let integrationName = item.integrationName;
		
		let itemTag = new ItemHeaderTag( integrationCode, integrationName );
		title.append( itemTag );

		let titleSentence = new ItemHeaderSentence( itemType, integrationCode, itemCode, itemId );
		title.append( titleSentence );
		container.append( title );

		let itemContent = $( '<div/>', { 'class': 'item__content' });
		container.append( itemContent );

		if ( itemType == 'action' && isDefined( UncannyAutomator.wp ) && UncannyAutomator.wp ) {
			let asyncButton = new ItemHeaderAsync( itemType, integrationCode, itemCode, itemId, container );

			container.append( asyncButton );
		}

		return container;
	}

	/**
	 * Create item notice
	 *
	 * @since 0.6
	 *
	 */

	createNotice( item ){
		let container;

		if ( item.isMissing ){
			container = $( '<div/>', { 'class': 'item-notice' });
			container.text( UncannyAutomator.i18n.itemMissing );
		}

		return container;
	}

	/**
	 * Create item notice
	 *
	 * @since 3.5
	 *
	 */

	 makeSortable( container ) {
		// Make actions sortable
		// Use sortable from the html5sortable library
		sortable( container, {
			placeholder: '<div class="item"></div>',
			handle: '.item-handle',
			items: '.item',
			hoverClass: 'hover',
		} )[0].addEventListener('sortupdate', (e) => {
			this.orderUpdated( e );
		});
	}

	orderUpdated( e ) {

		let parentContainer = e.detail.destination.container;

		// Set the container to load
		parentContainer.classList.add( 'loading' );

		// Get the HTML elements of the itmes in the new order
		let reorderedItems = e.detail.destination.items;

		let newOrder = [];
		
		// Loop through the items and collect their IDs
		$.each( reorderedItems, ( order, item ) => {
			newOrder.push( item.dataset.id );
		});

		// Send the array with IDs in the new order to the plugin
		restCall(
			'actions_order',
			{
				'recipeID': UncannyAutomator.recipe.id,
				'newOrder': newOrder
			},
			( response ) => {
				// In case of success
				new Actions();
				parentContainer.classList.remove( 'loading' );
			},
			( response ) => {
				// In case of a failure
				new Actions();

				parentContainer.classList.remove( 'loading' );
			}
			
		);
	}
}

export default MetaboxItems;