import $ from 'jquery';

// Import sprintf-js
const vsprintf = require( 'sprintf-js' ).vsprintf;

import {
	isDefined,
	getDOMElements,
	restCall,
}                         from '../common/utilities.js';

import ItemHeaderMenu     from './action-menu.js';
import ItemHeaderIcon     from './integration-icon.js';
import ItemHeaderTag      from './integration-name.js';
import ItemHeaderSentence from './sentence.js';

import Field              from '../field/field.js';

import Actions            from './actions.js';

class Closures {

	constructor( label ){
		// Create console group
		// console.group( 'Closures' );

		// Get metabox
		this.elements = {
			$metabox: getDOMElements( 'metabox', { type: 'action' })
		}

		// Create closures. It's important to understand that we're going to create all the available closures, not only those already added to the item
		// That's why we create a separate file instead of using Item.js
		$.each( UncannyAutomator.closures, ( key, closure ) => this.createClosure( closure, label ) );

		// Close console group
		// console.groupEnd();
	}

	createClosure( closure, label ){
		// Variable to check later if this closure already was added to the recipe
		let wasAdded = false;

		// Container
		let $container = $( `
			<div class="metabox__footer metabox__closures"></div>
		` );

		// Checkbox attributes. We're going to use this to create our checkbox later.
		let checkboxAttributes = {
			label:              label,
			formElementClass:   'form-element--nomargin'
		}

		// Check if this closure already was added to the recipe
		let closureInRecipe = this.getClosureByCodeFromItemsObject( closure.code );

		if ( isDefined( closureInRecipe ) ){
			wasAdded = true;

			checkboxAttributes.currentValue = true;
			checkboxAttributes.itemid       = closureInRecipe.id;

			// Debug
			// console.groupCollapsed( vsprintf( 'ID: %s', [ closureInRecipe.id ] ) );
			// console.log( closureInRecipe );
			// console.groupEnd();
		}

		// Create checkbox
		let checkboxField = new Field( 'checkbox', checkboxAttributes ),
			$checkbox      = checkboxField.getFormElement();

		// Bind changes
		$checkbox.on( 'change', function(){
			let isChecked = checkboxField.getValue();
			checkboxField.startLoadingAnimation();

			// Check if it's checked
			if ( isChecked ){
				// Add item to the recipe
				restCall( 'add', {
					recipePostID: UncannyAutomator.recipe.id,
					action:       'add-new-closure',
					item_code:    closure.code,
					default_meta: {
						integration_name: UncannyAutomator.integrations[ closure.integration ].name,
						sentence:         closure.sentence
					}
				}, ( response ) => {
					new Actions();

					// Check if the ID of the new item is defined
					if ( isDefined( response.post_ID ) ){
						// Get item form element
						const $newItem = getDOMElements( 'item', {
							id: response.post_ID
						});

						// Get first token and trigger a click
						$newItem.item.find( '.item-title__token:not(.item-title__token--filled)' ).first().trigger( 'click' );
					}
				}, ( response ) => {
					console.log( ' ERROR-TODO. Couldn\'t add the closure' );

					// Stop the loading animation
					checkboxField.stopLoadingAnimation();

					// Change back the value of the checkbox
					checkboxField.setValue( false, false );
				});
			}
			else {
				restCall( 'delete', {
					ID: closureInRecipe.id
				}, () => {
					new Actions();
				}, () => {
					console.log( ' ERROR-TODO. Couldn\'t delete the closure' );

					new Actions();
				});
			}
		});

		// Append checkbox to the container
		$container.append( $checkbox );

		// Create toggle section
		let $toggleContent = $( '<div/>', { 'class': 'uo-toggle uo-toggle--show' });

		// Check if it was already added. In that case show it
		if ( wasAdded ){
			$toggleContent.addClass( 'uo-toggle--show' );

			let $item = $( '<div/>', {
				'class':   'item item--closure item--margintop',
				'data-id': closureInRecipe.id
			});

			let $itemHeader = this.createHeader(
				UncannyAutomator.recipe.items[ closureInRecipe.id ].integrationCode,
				'closure',
				closure.code,
				closureInRecipe.id,
			);

			$item.append( $itemHeader );

			let $itemContent = $( '<div/>', { 'class': 'item__content' });
			$item.append( $itemContent );

			$toggleContent.append( $item );
		}
	
		$container.append( $toggleContent );

		// Append everything to the metabox
		this.elements.$metabox.append( $container );
	}

	/**
	 * Creates item header
	 * 
	 * @since 0.5
	 *
	 * @param {string}    integrationCode - The integration code
	 * @param {string}    itemType - The item type: 'trigger', 'action' or 'closure'
	 * @param {string}    itemCode - The code of the item
	 * @param {int}       itemId - The ID of the item
	 */

	createHeader( integrationCode, itemType, itemCode, itemId ){
		let $container = $( '<div/>' ),
			$title     = $( '<div/>', { 'class': 'item-title uap-clear' });

		let $actions = new ItemHeaderMenu( itemType, integrationCode, itemCode, itemId );
		$container.append( $actions );

		let $icon    = new ItemHeaderIcon( integrationCode );
		$container.append( $icon );
		
		let $itemTag = new ItemHeaderTag( integrationCode );
		$title.append( $itemTag );

		let $titleSentence = new ItemHeaderSentence( itemType, integrationCode, itemCode, itemId );
		$title.append( $titleSentence );

		$container.append( $title );

		return $container;
	}

	getClosureByCodeFromItemsObject( code ){
		let theItem;

		$.each( UncannyAutomator.recipe.items, function( itemID, item ){
			if ( item.itemCode == code ){
				theItem = item;
			}
		})

		return theItem;
	}
}

export default Closures;